:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

header {
  z-index: 100;
  width: 100%;
  background-color: #00f;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.search-form {
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.back-ref {
  color: #ff0;
  margin-left: 20px;
}

.search-form input {
  height: 32px;
  width: 400px;
  font: inherit;
  margin: 0;
  padding: 0 0 0 6px;
}

.btn-search {
  height: 32px;
  cursor: pointer;
  align-self: center;
  margin: 0;
}

.icon-search {
  width: 15px;
  height: 15px;
  stroke-width: 0;
  stroke: currentColor;
  fill: #000;
  display: inline-block;
}

.load-more {
  background-color: gray;
  border-radius: 2px;
  margin-left: auto;
  margin-right: auto;
  padding: 6px 12px;
  display: block;
}

.label {
  font-weight: bold;
}

.is-hidden, .button > .spinner.is-hidden {
  display: none;
}

.no-result {
  color: #00f;
  align-self: center;
}

